@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Lobster&family=Roboto+Mono:wght@400;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

body {
  color: #e0e0e0;
  text-align: center;
  background-color: #121212;
  flex-direction: column;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.sign-in-main {
  background: radial-gradient(circle, #1c1c1c, #121212);
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.sign-in-container {
  text-align: center;
  background: #141414f2;
  border: 1px solid #333;
  border-radius: 16px;
  width: 360px;
  max-width: 90%;
  padding: 2.5rem;
  animation: .5s ease-out fadeIn;
  box-shadow: 0 12px 30px #000000b3;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(.95);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.sign-in-container h2 {
  color: #bb86fc;
  margin-bottom: 1rem;
  font-family: Lobster, cursive;
  font-size: 2.4rem;
}

.sign-in-container p {
  color: #a0a0a0;
  margin-bottom: 2rem;
  font-size: 1.2rem;
}

#googleSignInBtn {
  color: #121212;
  cursor: pointer;
  background-color: #bb86fc;
  border: none;
  border-radius: 4px;
  padding: .9rem 1.8rem;
  font-size: 1.1rem;
  transition: background-color .3s, transform .2s;
}

#googleSignInBtn:hover {
  background-color: #9f6efb;
  transform: scale(1.05);
}

header {
  color: #e0e0e0;
  text-align: center;
  background-color: #1f1f1f;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 20px;
  font-family: Lobster, cursive;
  font-size: 2rem;
}

nav {
  display: none;
}

#book-form-section {
  background: #1e1e1e;
  border-radius: 10px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0009;
}

form input, form select, form button {
  color: #e0e0e0;
  background-color: #2a2a2a;
  border: 1px solid #333;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
}

form button {
  color: #121212;
  cursor: pointer;
  background-color: #bb86fc;
  border: none;
  font-size: 1rem;
  transition: background-color .3s;
}

form button:hover {
  background-color: #9f6efb;
}

#book-list-section {
  background: #1e1e1e;
  border-radius: 10px;
  max-width: 600px;
  margin: auto auto 0;
  padding: 20px;
  font-family: Roboto Mono, monospace;
  box-shadow: 0 4px 8px #0009;
}

#book-list-section h2 {
  color: #bb86fc;
  margin-bottom: 1rem;
}

ul#bookList {
  margin-top: 10px;
  list-style: none;
}

ul#bookList li {
  background: #2a2a2a;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  margin: 10px 0;
  padding: 15px;
  transition: background .3s;
  display: flex;
  box-shadow: 0 2px 4px #00000080;
}

ul#bookList li:hover {
  background: #333;
}

ul#bookList li span {
  flex: 1;
  margin-right: 1rem;
  line-height: 1.4;
}

.sidebar {
  text-align: left;
  background-color: #1e1e1e;
  border: 1px solid #333;
  border-radius: 10px;
  width: 320px;
  padding: 1rem;
}

.sidebar h2 {
  color: #9778bd;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.6rem;
  font-weight: bold;
}

.sidebar select {
  color: #e0e0e0;
  cursor: pointer;
  background-color: #2a2a2a;
  border: 1px solid #333;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 1rem;
  padding: 10px;
  font-size: 1rem;
  font-weight: 500;
  display: block;
}

.sidebar select option {
  color: #e0e0e0;
  background-color: #2a2a2a;
  padding: 6px;
  font-size: 1rem;
}

.container {
  justify-content: center;
  gap: 20px;
  max-width: 1200px;
  margin: auto;
  display: flex;
}

.main-content {
  flex: 1;
}

.edit-btn, .delete-btn {
  color: #121212;
  cursor: pointer;
  background-color: #bb86fc;
  border: none;
  border-radius: 4px;
  margin: 4px;
  padding: 8px 12px;
  font-size: 1rem;
  transition: background-color .3s, transform .2s;
}

.delete-btn {
  background-color: #ff6f91;
}

.delete-btn:hover {
  background-color: #ff3b6f;
}

#chat-widget {
  z-index: 999;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

#chat-circle {
  color: #121212;
  cursor: pointer;
  background-color: #bb86fc;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 40px;
  display: flex;
  position: relative;
  box-shadow: 0 4px 8px #00000080;
}

#chat-circle:after {
  content: "";
  border: 8px solid #0000;
  border-top-color: #bb86fc;
  width: 0;
  height: 0;
  position: absolute;
  bottom: -8px;
  left: 10px;
}

#chatbot-container {
  background-color: #1e1e1e;
  border: 1px solid #444;
  width: 300px;
  padding: 10px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  overflow: hidden;
  box-shadow: 0 4px 6px #000000b3;
}

.chat-widget-open #chatbot-container {
  display: block;
}

#chatbot-header {
  color: #121212;
  text-align: center;
  background-color: #bb86fc;
  border-radius: 10px 10px 0 0;
  padding: 10px;
  font-size: 1rem;
  font-weight: bold;
}

#chat-history {
  background-color: #1e1e1e;
  border-bottom: 1px solid #444;
  height: 200px;
  padding: 10px;
  overflow-y: auto;
}

#chat-input-area {
  box-sizing: border-box;
  background-color: #1e1e1e;
  gap: 5px;
  width: 100%;
  padding: 10px;
  display: flex;
}

#chat-input {
  color: #e0e0e0;
  background-color: #2a2a2a;
  border: 1px solid #444;
  border-radius: 5px;
  flex: 1;
  padding: 8px;
  font-size: 1rem;
}

#send-btn, #chat-close-btn {
  color: #121212;
  cursor: pointer;
  background-color: #bb86fc;
  border: none;
  border-radius: 5px;
  padding: 5px 9px;
  font-size: 1rem;
  transition: background-color .3s;
}

#send-btn:hover {
  background-color: #9f6efb;
}

.chat-message {
  text-align: left;
  margin-bottom: 10px;
  line-height: 1.4;
}

.chat-message.user-message {
  color: #e0e0e0;
  font-weight: bold;
}

.chat-message.bot-message {
  color: #a0a0a0;
}

.modal {
  z-index: 100;
  background-color: #000c;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  color: #e0e0e0;
  background-color: #1e1e1e;
  border: 1px solid #444;
  border-radius: 8px;
  width: 80%;
  max-width: 500px;
  margin: 10% auto;
  padding: 20px;
}

.modal-content p {
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.modal-content button {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 20px;
}

#confirmDelete {
  color: #121212;
  background-color: #ff6f91;
}

#cancelDelete {
  color: #121212;
  background-color: pink;
}

.close-btn {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

.close-btn:hover, .close-btn:focus {
  color: #fff;
  text-decoration: none;
}

#confirmSignOut {
  color: #121212;
  background-color: #ff6f91;
}

#cancelSignOut {
  color: #121212;
  background-color: pink;
}

#signOutBttn {
  color: #121212;
  cursor: pointer;
  background-color: #bb86fc;
  border: none;
  border-radius: 4px;
  padding: .5rem 1rem;
  font-size: 1rem;
  transition: background-color .3s;
}

#signOutBttn:hover {
  background-color: #9f6efb;
}

.error-message {
  color: #ff6f91;
  margin-top: 4px;
  font-size: .85rem;
  display: block;
}
